@import '../common/util';
@import "partials/normalize";

.event-cont {
    margin: 78px 0 80px;
    h3 {
        font-size: 22px;
        color: #1f1f1f;
    }
    .category-menu {
        margin-top: -20px;
        margin-bottom: 60px;
    }
    .event-wrap {
        .event-list {
            margin-left: -20px;
            &::after {
                content: '';
                display: table;
                clear: both;
            }
            .item {
                margin-bottom: 20px;
            }
            .link {
                display: block;
                height: 45px;
            }
            .list {
                display: block;
                color: #1f1f1f;
                position: relative;
                float: left;
                width: 310px;
                margin-left: 20px;
                margin-bottom: 45px;
                .thumb {
                    margin-bottom: 25px;
                }
                .title {
                    margin-bottom: 12px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    word-wrap: break-word;
                    a {
                        color: #1f1f1f;
                        font-size: 15px;
                        line-height: 1.4;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        overflow: hidden;
                        display: inline-block;
                        font-weight: bold;
                        max-width: 100%;
                    }
                }
                .desc {
                    margin-bottom: 10px;
                    font-size: 13px;
                    color: #1f1f1f;
                }
                .date {
                    font-size: 13px;
                    color: #aaa;
                }
            }
        }
        .list-more {
            margin: 0 auto;
        }
    }
}
.event-detail {
    margin-top: 70px;
    h3.event-detail-title {
        margin-bottom: 15px;
        font-size: 18px;
        color: #1f1f1f;
        font-weight: bold;
    }
    .location {
        &>span, &>a {
            position: relative;
            display: inline-block;
            padding: 0 11px;
            font-size: 13px;
            color: #898989;
            &::before {
                content: '';
                position: absolute;
                left: 0;
                top: 1px;
                width: 1px;
                height: 12px;
                background-color: #e0e0e0;
            }
        }
        span {
            &:first-child {
                padding-left: 0;
                color: #1f1f1f;
                a {
                    color: #1f1f1f;
                }
            }
            &:first-child::before {
                display: none;
            }
        }
        a:hover {
            text-decoration: underline;
        }
    }
    .detail-cont {
        .detail-cont-wrapper {
            @include clear-content();
            padding: 45px 0;
            text-align: center;
            img {
                margin: 0 auto;
            }
        }
        .detail-img {
            position: absolute;
            left: 0;
            top: 288px;
            right: 0;
            text-align: center;
            overflow: hidden;
            img {
                position: relative;
                z-index: 1;
                display: inline-block;
                width: 1200px;
                vertical-align: top;
            }
            span {
                position: absolute;
                right: 0;
                bottom: 0;
                width: 972px;
                height: 638px;
                font: 0/0 a;
                background: url('/_ui/img/pc/temp/@event-percent-5.png') no-repeat;
            }
        }
    }
    .prod-wrap {
        padding-top: 80px;
        border-top: 1px solid #e8e8e8;
        .tit {
            margin-bottom: 40px;
            font-weight: bold;
            text-align: center;
            font-size: 15px;
            color: #0f0f10;
        }
        .prod-list {
            margin-left: -20px;
            &::after {
                content: '';
                display: table;
                clear: both;
            }
            .list {
                color: #1f1f1f;
                position: relative;
                float: left;
                width: 225px;
                height: 432px;
                margin-left: 19px;
                margin-bottom: 70px;
            .thumb {
                margin-bottom: 10px;
            }
            .brand {
                font-size: 11px;
                font-weight: bold;
                display: inline-block;
                height: 20px;
                margin: 0 10px 15px;
                padding-bottom: 5px;
                border-bottom: 3px solid #1f1f1f;
                box-sizing: border-box;
            }
            .title {
                margin: 0 10px 18px;
                a {
                color: #1f1f1f;
                font-size: 13px;
                line-height: 1.4;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
                display: inline-block;
                max-width: 100%;
                }
            }
            .price {
                font-size: 12px;
                margin: 0 10px;
                del {
                color: #9ba4b0;
                }
            }
            .sale {
                font-size: 12px;
                font-weight: bold;
                position: absolute;
                right: 10px;
                bottom: 0;
                padding-left: 12px;
                background: url('/_ui/img/pc/icn/icn-sale.png') no-repeat 0 50%;
            }
            }
        }
        .list-more {
            margin: 0 auto 80px;
        }
    }
    .buttons {
        margin : 60px auto 140px;
        text-align: center;
    }
}